$primary: #00DCFF;
$primayDark: #0396ad;
$textPrimary: #011F27;
a {
    color: $primary;
    &:hover,
    &:focus {
        color: $primayDark;
    }
    .button {
        color: #fff;
    }
}

.map {
    height: 500px;
}

@media(max-width:768px) {
    .map {
        height: 75%;
    }
}

.map iframe {
    pointer-events: none;
}

hr {
    height: 0.1rem;
    background-color: #00FFFF;
}

footer p {
    font-size: 20px;
}

.btn-default {
    color: $primary;
    border: 1px solid $primary;
    background-color: transparent;
    &:focus,
    &:hover {
        border: 1px solid $primary;
        background-color: $primary;
    }
}

img.img-v-logo {
    width: 50%;
}

