a {
  color: #00DCFF;
}

a:hover, a:focus {
  color: #0396ad;
}

a .button {
  color: #fff;
}

.map {
  height: 500px;
}

@media (max-width: 768px) {
  .map {
    height: 75%;
  }
}

.map iframe {
  pointer-events: none;
}

hr {
  height: 0.1rem;
  background-color: #00FFFF;
}

footer p {
  font-size: 20px;
}

.btn-default {
  color: #00DCFF;
  border: 1px solid #00DCFF;
  background-color: transparent;
}

.btn-default:focus, .btn-default:hover {
  border: 1px solid #00DCFF;
  background-color: #00DCFF;
}

img.img-v-logo {
  width: 50%;
}

.gradientoverlay {
  background: -moz-linear-gradient(to bottom right, rgba(0, 47, 75, 0.5) 0%, #dc4225 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(to bottom right, rgba(0, 47, 75, 0.5) 0%, #dc4225 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom right, rgba(0, 47, 75, 0.5) 0%, #dc4225 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
}

@media (min-width: 992px) {
  .content-section {
    padding-top: 100px;
    padding-bottom: 200px;
  }
}

#info {
  background-image: url("../img/circuit-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.card {
  border: none;
  border-radius: none;
}

.card-text {
  color: #011F27;
}

.tagline {
  font-family: 'Open Sans Condensed', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.video-complement {
  text-transform: none;
  font-family: 'Open Sans Condensed', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

canvas {
  display: block;
  vertical-align: bottom;
  height: 80vh;
  width: 100vw;
  background-attachment: fixed;
}
